import { Component, Vue } from 'vue-property-decorator'
import { getDeviceList, updateDeviceInfo } from '@/api/contract/contractApi'
import { getProjectList } from '@/api/manage/sys/houseManageApi'
import {
  getRoomTypeList,
  getBuildingSelect,
  getRoomList,
} from '@/api/manage/sys/housePageApi'
@Component({
  components: {},
})
export default class water extends Vue {
  // title
  private pageTitle = '水表'
  // 表格数据源
  private dataSource: any[] = []
  // 是否显示dialog
  private dialogFormVisible = false
  // 设备初始化
  private initDevice: {
    deviceCode: any
    deviceName: any
    deviceType: any
    id: any
    projectId: any
    roomId: any
  } = {
    deviceName: '',
    deviceCode: '',
    deviceType: '',
    id: '',
    projectId: '',
    roomId: '',
  }
  // currentDevice 当前要修改的设备/要新增的设备
  private currentDevice: {
    deviceCode: any
    deviceName: any
    deviceType: any
    id: any
    projectId: any
    roomId: any
  } = {
    deviceName: '',
    deviceCode: '',
    deviceType: '',
    id: '',
    projectId: '',
    roomId: '',
  }
  // 页码相关
  private pagination = {
    total: 0,
    current: 1,
    size: 10,
  }
  // 查询参数
  private query = {
    buildingNo: '',
    deviceType: 'W',
    keyword: '',
    projectId: '',
    roomId: '',
  }
  // 房间相关
  private queryRoom = {
    projectId: '', // 项目id
    rootTypeId: '', // 房型id
    buildingNo: '', // 楼栋编码
    roomId: '', // 房间的id
  }
  // 项目列表
  private projectList: any[] = []
  // 房型列表
  private roomTypeList: any[] = []
  // 楼栋列表
  private buidingNoList: any[] = []
  // 房间列表
  private roomList: any[] = []
  // 搜索条件显示
  private open = true
  private toggle() {
    this.open = !this.open
  }
  get isConditionText() {
    if (this.open) {
      return '收起条件'
    }
    return '展开条件'
  }
  //改变页码
  private handleCurrentChange(e: any) {
    this.pagination.current = e
    this.getList()
  }
  // 点击项目下拉
  private projectOnInput(e: any) {
    this.queryRoom.rootTypeId = ''
    this.queryRoom.buildingNo = ''
    this.queryRoomTypeList()
  }
  // 点击房型下拉
  private roomTypeOnInput(e: any) {
    this.queryRoom.buildingNo = ''
    this.queryBuildingList()
  }
  // 点击楼栋下拉
  private buildingNoOnInput(e: any) {
    this.queryRoom.roomId = ''
    this.queryRoomList()
  }
  // 点击房间下拉
  private roomOnInput(e: any) {
    this.getList()
  }
  // 获取设备列表
  private async getList() {
    this.query = Object.assign(this.query, this.pagination)
    this.query = Object.assign(this.query, this.queryRoom)
    const res = await getDeviceList(this.query)
    if (res.code === 200) {
      this.dataSource = res.data.records
      this.pagination.total = res.data.total
    }
  }
  // 获取项目列表
  private async queryProjectList() {
    const res = await getProjectList()
    if (res.code === 200) {
      this.projectList = res.data
      console.log('project', this.projectList)
    }
  }
  // 获取房型列表
  private async queryRoomTypeList() {
    const res = await getRoomTypeList({ projectId: this.queryRoom.projectId })
    if (res.code === 200) {
      this.roomTypeList = res.data
    }
  }
  // 获取楼栋列表
  private async queryBuildingList() {
    const res = await getBuildingSelect({
      projectId: this.queryRoom.projectId,
      rootTypeId: this.queryRoom.rootTypeId,
    })
    if (res.code === 200) {
      this.buidingNoList = res.data
    }
  }

  // 获取房间列表
  private async queryRoomList() {
    const res = await getRoomList({
      buildingNo: this.queryRoom.buildingNo,
      projectId: this.queryRoom.projectId,
      rootTypeId: this.queryRoom.rootTypeId,
    })
    if (res.code === 200) {
      this.roomList = res.data
    }
  }
  // 修改设备信息
  private changeDevice(scope: any) {
    console.log('scope', scope.row)
    this.dialogFormVisible = true
    this.currentDevice = Object.assign({}, scope.row)
  }
  // 保存修改的设备信息
  private async saveDeviceInfo() {
    const res = await updateDeviceInfo(this.currentDevice)
    if (res.code === 200) {
      this.dialogFormVisible = false
      this.$message.success('保存成功')
      await this.getList()
      this.currentDevice = Object.assign({}, this.initDevice)
    }
  }
  public mounted(): void {
    this.getList()
    this.queryProjectList()
  }
}
